.inventory-management {
  width: 100%;
  padding: 2rem;
  font-family: 'Inter', sans-serif;
  background: var(--background-color);
}

/* Header */
.inventory-header {
  text-align: center;
  margin-bottom: 2rem;
}

.inventory-header h2 {
  color: var(--text-primary);
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.inventory-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
}

/* Controls */
.inventory-controls {
  text-align: center;
  margin-bottom: 2rem;
}

.refresh-button {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--primary-color) 100%);
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.refresh-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-hover);
}

.refresh-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Error */
.error-message {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  text-align: center;
}

/* Recommendations Grid */
.recommendations-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 1.5rem;
}

/* Recommendation Card */
.recommendation-card {
  background: var(--card-background);
  border-radius: 12px;
  padding: 1.5rem;
  box-shadow: var(--shadow);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.recommendation-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-hover);
}

/* Card Header */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.card-header h3 {
  font-size: 1.2rem;
  font-weight: 700;
  color: var(--text-primary);
}

.priority-badge {
  padding: 0.25rem 0.5rem;
  border-radius: 4px;
  font-weight: 600;
  font-size: 0.75rem;
  color: white;
  text-transform: uppercase;
}

.priority-badge.critical {
  background: #dc2626;
}

.priority-badge.high {
  background: #f59e0b;
}

.priority-badge.medium {
  background: #3b82f6;
}

/* Metrics */
.inventory-metrics {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(140px, 1fr));
  gap: 1rem;
  margin-bottom: 1rem;
}

.metric {
  background: var(--background-color);
  padding: 0.75rem 1rem;
  border-radius: 6px;
  display: flex;
  flex-direction: column;
}

.metric-label {
  font-weight: 500;
  color: var(--text-secondary);
  margin-bottom: 0.25rem;
  font-size: 0.85rem;
}

.metric-value {
  font-weight: 700;
  color: var(--text-primary);
  font-size: 1.1rem;
}

/* Reasoning */
.reasoning {
  margin-bottom: 1rem;
  color: var(--text-secondary);
  font-size: 0.95rem;
}

/* Card Actions */
.card-actions {
  display: flex;
  gap: 0.75rem;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.action-button {
  flex: 1;
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

.action-button:hover {
  background: var(--accent-color);
  transform: translateY(-1px);
}

.details-button {
  flex: 1;
  background: var(--card-background);
  color: var(--text-primary);
  border: 1px solid var(--border-color);
  padding: 0.5rem 1rem;
  border-radius: 6px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.details-button:hover {
  background: var(--background-color);
}

/* Detailed Info */
.detailed-info {
  background: var(--background-color);
  padding: 1rem;
  border-radius: 6px;
  border-top: 1px solid var(--border-color);
  font-size: 0.9rem;
  color: var(--text-secondary);
}

.detailed-info h4 {
  margin-bottom: 0.5rem;
  font-size: 1rem;
  color: var(--text-primary);
}

/* No Recommendations */
.no-recommendations {
  text-align: center;
  padding: 3rem;
  color: var(--text-secondary);
  background: var(--card-background);
  border-radius: 12px;
  box-shadow: var(--shadow);
}

/* Responsive Design */
@media (max-width: 768px) {
  .inventory-controls {
    text-align: center;
  }

  .inventory-metrics {
    grid-template-columns: 1fr;
  }

  .card-actions {
    flex-direction: column;
  }
}
