.demand-forecasting {
  width: 100%;
  padding: 2rem;
  font-family: 'Inter', sans-serif;
}

.forecast-header {
  text-align: center;
  margin-bottom: 2rem;
}

.forecast-header h2 {
  color: var(--text-primary);
  font-size: 2.2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
}

.forecast-header p {
  color: var(--text-secondary);
  font-size: 1.1rem;
}

.forecast-controls {
  display: flex;
  gap: 1.5rem;
  align-items: flex-end;
  margin-bottom: 2rem;
  padding: 1.5rem;
  background: var(--card-background);
  border-radius: 12px;
  box-shadow: var(--shadow);
}

.control-group {
  flex: 1;
}

.control-group label {
  display: block;
  margin-bottom: 0.5rem;
  color: var(--text-primary);
  font-weight: 500;
}

.control-group select {
  width: 100%;
  padding: 0.75rem;
  border: 2px solid var(--border-color);
  border-radius: 6px;
  font-size: 1rem;
  background: var(--background-color);
  transition: border 0.3s ease;
}

.control-group select:focus {
  border-color: var(--accent-color);
  outline: none;
}

.forecast-button {
  background: linear-gradient(135deg, var(--accent-color) 0%, var(--primary-color) 100%);
  color: white;
  padding: 0.75rem 2rem;
  border-radius: 8px;
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}

.forecast-button:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: var(--shadow-hover);
}

.forecast-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.forecast-results {
  background: var(--card-background);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: var(--shadow);
}

.forecast-results h3 {
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  font-size: 1.5rem;
}

.forecast-summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.summary-card {
  background: var(--background-color);
  padding: 1.5rem;
  border-radius: 8px;
  text-align: center;
  border-left: 4px solid var(--accent-color);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.summary-card:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-hover);
}

.summary-card h4 {
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
  text-transform: uppercase;
}

.summary-value {
  color: var(--text-primary);
  font-size: 2rem;
  font-weight: 700;
}

.forecast-details h4 {
  color: var(--text-primary);
  margin-bottom: 1rem;
  font-size: 1.2rem;
}

.forecast-table {
  border: 1px solid var(--border-color);
  border-radius: 8px;
  overflow: hidden;
}

.table-header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  gap: 1rem;
  padding: 1rem;
  background: var(--primary-color);
  color: white;
  font-weight: 600;
  text-transform: uppercase;
}

.table-row {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr;
  gap: 1rem;
  padding: 1rem;
  border-bottom: 1px solid var(--border-color);
  background: var(--card-background);
  transition: background 0.3s ease;
}

.table-row:nth-child(even) {
  background: var(--background-color);
}

.table-row:last-child {
  border-bottom: none;
}

.table-row:hover {
  background: rgba(59, 130, 246, 0.1); /* subtle hover highlight */
}

.error-message {
  background: rgba(239, 68, 68, 0.1);
  color: #dc2626;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1rem;
  text-align: center;
}

/* Responsive design */
@media (max-width: 768px) {
  .forecast-controls {
    flex-direction: column;
    align-items: stretch;
  }

  .forecast-summary {
    grid-template-columns: 1fr;
  }

  .table-header,
  .table-row {
    grid-template-columns: 1fr;
    gap: 0.5rem;
  }
}
