/* frontend/src/components/Dashboard.css */

/* Enhanced Futuristic Cyberpunk Dashboard Variables */
:root {
  /* Neon Color Palette */
  --neon-cyan: #00ffff;
  --neon-purple: #8b5cf6;
  --neon-blue: #3b82f6;
  --neon-pink: #ec4899;
  --neon-green: #10b981;
  --neon-red: #ef4444;
  --neon-yellow: #f59e0b;

  /* Dark Space Theme */
  --space-bg: #000000;
  --space-black: #000011;
  --glass-bg: rgba(0, 0, 0, 0.7);
  --glass-border: rgba(0, 255, 255, 0.2);
  --text-primary: #ffffff;
  --text-secondary: #a0a0a0;

  /* Typography */
  --font-family-cyber: 'Courier New', 'Monaco', monospace;
  --font-family-ui: 'Orbitron', 'Rajdhani', sans-serif;

  /* Spacing */
  --spacing-1: 0.25rem;
  --spacing-2: 0.5rem;
  --spacing-3: 0.75rem;
  --spacing-4: 1rem;
  --spacing-5: 1.25rem;
  --spacing-6: 1.5rem;
  --spacing-8: 2rem;

  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;

  /* Shadows & Glows */
  --shadow-glow-cyan: 0 0 30px var(--neon-cyan), 0 0 60px var(--neon-cyan), 0 0 90px var(--neon-cyan), 0 0 120px var(--neon-cyan);
  --shadow-glow-purple: 0 0 30px var(--neon-purple), 0 0 60px var(--neon-purple), 0 0 90px var(--neon-purple);
  --shadow-glow-blue: 0 0 30px var(--neon-blue), 0 0 60px var(--neon-blue), 0 0 90px var(--neon-blue);
  --shadow-glow-green: 0 0 30px var(--neon-green), 0 0 60px var(--neon-green), 0 0 90px var(--neon-green);
  --shadow-glow-red: 0 0 30px var(--neon-red), 0 0 60px var(--neon-red), 0 0 90px var(--neon-red);
  --shadow-glow-yellow: 0 0 30px var(--neon-yellow), 0 0 60px var(--neon-yellow), 0 0 90px var(--neon-yellow);
  --glass-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

/* Full Body Futuristic Dashboard */
.dashboard, .advanced-dashboard {
  padding: 0;
  max-width: 100vw;
  margin: 0;
  width: 100vw;
  min-height: 100vh;
  background: var(--space-bg);
  position: relative;
  box-sizing: border-box;
  font-family: var(--font-family-ui);
  overflow: hidden;
  perspective: 1000px;
}

/* Matrix Rain and Cyber Grid Effects for Full Body */
.matrix-rain {
  position: relative;
  overflow: hidden;
}

.matrix-rain::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: repeating-linear-gradient(
    0deg,
    transparent,
    transparent 2px,
    rgba(0, 255, 255, 0.03) 2px,
    rgba(0, 255, 255, 0.03) 4px
  );
  animation: matrix-rain 20s linear infinite;
  z-index: -1;
  pointer-events: none;
}

@keyframes matrix-rain {
  0% { transform: translateY(0) skewX(0deg); }
  100% { transform: translateY(-100vh) skewX(5deg); }
}

.cyber-grid {
  position: relative;
}

.cyber-grid::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-image:
    linear-gradient(var(--neon-cyan) 1px, transparent 1px),
    linear-gradient(90deg, var(--neon-cyan) 1px, transparent 1px);
  background-size: 50px 50px;
  opacity: 0.05;
  animation: grid-move 30s linear infinite;
  z-index: -1;
  pointer-events: none;
}

@keyframes grid-move {
  0% { transform: translate(0, 0); }
  100% { transform: translate(50px, 50px); }
}

/* Full Body Starry Background Layer 1 */
.dashboard::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background:
    radial-gradient(4px 4px at 20px 30px, #eee, transparent),
    radial-gradient(3px 3px at 40px 70px, rgba(0,255,255,0.8), transparent),
    radial-gradient(2px 2px at 90px 40px, #fff, transparent),
    radial-gradient(2px 2px at 130px 80px, rgba(139,92,246,0.6), transparent),
    radial-gradient(4px 4px at 160px 30px, #eee, transparent),
    radial-gradient(2px 2px at 200px 90px, rgba(59,130,246,0.7), transparent),
    radial-gradient(3px 3px at 250px 60px, #fff, transparent),
    radial-gradient(2px 2px at 300px 20px, rgba(16,185,129,0.5), transparent),
    radial-gradient(4px 4px at 350px 80px, #eee, transparent),
    radial-gradient(2px 2px at 400px 40px, rgba(236,72,153,0.8), transparent),
    radial-gradient(3px 3px at 450px 70px, #fff, transparent),
    radial-gradient(2px 2px at 500px 30px, rgba(239,68,68,0.6), transparent),
    radial-gradient(4px 4px at 550px 90px, #eee, transparent),
    radial-gradient(2px 2px at 600px 50px, rgba(245,158,11,0.7), transparent),
    radial-gradient(3px 3px at 650px 20px, #fff, transparent),
    radial-gradient(2px 2px at 700px 80px, rgba(0,255,255,0.5), transparent),
    radial-gradient(4px 4px at 750px 40px, #eee, transparent),
    radial-gradient(2px 2px at 800px 70px, rgba(139,92,246,0.8), transparent),
    radial-gradient(3px 3px at 850px 30px, #fff, transparent),
    radial-gradient(2px 2px at 900px 60px, rgba(59,130,246,0.6), transparent),
    radial-gradient(4px 4px at 950px 90px, #eee, transparent),
    radial-gradient(2px 2px at 1000px 20px, rgba(16,185,129,0.7), transparent),
    radial-gradient(3px 3px at 1050px 50px, #fff, transparent),
    radial-gradient(2px 2px at 1100px 80px, rgba(236,72,153,0.5), transparent),
    radial-gradient(4px 4px at 1150px 30px, #eee, transparent),
    radial-gradient(2px 2px at 1200px 70px, rgba(239,68,68,0.8), transparent),
    radial-gradient(3px 3px at 1250px 40px, #fff, transparent),
    radial-gradient(2px 2px at 1300px 90px, rgba(245,158,11,0.6), transparent),
    radial-gradient(4px 4px at 1350px 60px, #eee, transparent),
    radial-gradient(2px 2px at 1400px 20px, rgba(0,255,255,0.7), transparent),
    radial-gradient(3px 3px at 1450px 80px, #fff, transparent),
    radial-gradient(2px 2px at 1500px 30px, rgba(139,92,246,0.5), transparent),
    radial-gradient(4px 4px at 1550px 70px, #eee, transparent),
    radial-gradient(2px 2px at 1600px 50px, rgba(59,130,246,0.8), transparent),
    radial-gradient(3px 3px at 1650px 90px, #fff, transparent),
    radial-gradient(2px 2px at 1700px 20px, rgba(16,185,129,0.6), transparent),
    radial-gradient(4px 4px at 1750px 60px, #eee, transparent),
    radial-gradient(2px 2px at 1800px 40px, rgba(236,72,153,0.7), transparent),
    radial-gradient(3px 3px at 1850px 80px, #fff, transparent),
    radial-gradient(2px 2px at 1900px 30px, rgba(239,68,68,0.5), transparent),
    radial-gradient(4px 4px at 1950px 70px, #eee, transparent);
  background-size: 2000px 200px;
  animation: twinkle 3s linear infinite, move-stars 15s linear infinite;
  z-index: -2;
  pointer-events: none;
}

@keyframes twinkle {
  0%, 100% { opacity: 0.4; }
  50% { opacity: 1; }
}

@keyframes move-stars {
  0% { transform: translateX(0) translateY(0); }
  100% { transform: translateX(-200px) translateY(-100px); }
}

/* Full Body Glitter Particles Layer 2 */
.dashboard::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background:
    radial-gradient(circle at 10% 20%, rgba(0,255,255,0.6) 2px, transparent 2px),
    radial-gradient(circle at 30% 40%, rgba(139,92,246,0.5) 2px, transparent 2px),
    radial-gradient(circle at 50% 60%, rgba(255,255,255,0.8) 2px, transparent 2px),
    radial-gradient(circle at 70% 80%, rgba(59,130,246,0.4) 2px, transparent 2px),
    radial-gradient(circle at 90% 10%, rgba(16,185,129,0.7) 2px, transparent 2px),
    radial-gradient(circle at 20% 90%, rgba(236,72,153,0.6) 2px, transparent 2px),
    radial-gradient(circle at 40% 30%, rgba(239,68,68,0.5) 2px, transparent 2px),
    radial-gradient(circle at 60% 70%, rgba(245,158,11,0.8) 2px, transparent 2px),
    radial-gradient(circle at 80% 50%, rgba(0,255,255,0.4) 2px, transparent 2px),
    radial-gradient(circle at 15% 85%, rgba(139,92,246,0.7) 2px, transparent 2px),
    radial-gradient(circle at 35% 15%, rgba(59,130,246,0.6) 2px, transparent 2px),
    radial-gradient(circle at 55% 95%, rgba(16,185,129,0.5) 2px, transparent 2px),
    radial-gradient(circle at 75% 25%, rgba(236,72,153,0.8) 2px, transparent 2px),
    radial-gradient(circle at 95% 75%, rgba(239,68,68,0.4) 2px, transparent 2px),
    radial-gradient(circle at 25% 45%, rgba(245,158,11,0.7) 2px, transparent 2px),
    radial-gradient(circle at 45% 65%, rgba(255,255,255,0.6) 2px, transparent 2px),
    radial-gradient(circle at 65% 35%, rgba(0,255,255,0.5) 2px, transparent 2px),
    radial-gradient(circle at 85% 55%, rgba(139,92,246,0.8) 2px, transparent 2px),
    radial-gradient(circle at 5% 75%, rgba(59,130,246,0.4) 2px, transparent 2px),
    radial-gradient(circle at 95% 25%, rgba(16,185,129,0.7) 2px, transparent 2px);
  background-size: 150px 150px;
  animation: glitter 2s ease-in-out infinite alternate, rotate-stars 20s linear infinite;
  z-index: -1;
  pointer-events: none;
}

@keyframes glitter {
  0% { opacity: 0.3; transform: scale(1) rotate(0deg); }
  50% { opacity: 0.9; transform: scale(1.2) rotate(180deg); }
  100% { opacity: 0.5; transform: scale(0.8) rotate(360deg); }
}

@keyframes rotate-stars {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Data Stream Effect for Full Body */
.data-stream {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: repeating-linear-gradient(
    90deg,
    transparent,
    transparent 10px,
    rgba(0, 255, 255, 0.05) 10px,
    rgba(0, 255, 255, 0.05) 20px
  );
  animation: data-stream 10s linear infinite;
  z-index: -1;
  pointer-events: none;
}

@keyframes data-stream {
  0% { transform: translateX(0); }
  100% { transform: translateX(-20px); }
}

/* Floating Particles for Full Body */
.particles {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  pointer-events: none;
  z-index: 0;
}

.particle {
  position: absolute;
  background: var(--neon-cyan);
  border-radius: 50%;
  opacity: 0.6;
  animation: particle-float linear infinite;
  box-shadow: var(--shadow-glow-cyan);
}

@keyframes particle-float {
  0% {
    transform: translateY(100vh) rotate(0deg);
    opacity: 0;
  }
  10% {
    opacity: 0.8;
  }
  90% {
    opacity: 0.8;
  }
  100% {
    transform: translateY(-100px) rotate(360deg);
    opacity: 0;
  }
}

/* Connection Status with Enhanced Glow */
.connection-status {
  position: fixed;
  top: var(--spacing-2);
  right: var(--spacing-2);
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  padding: var(--spacing-2) var(--spacing-3);
  border-radius: var(--radius-lg);
  display: flex;
  align-items: center;
  gap: var(--spacing-1);
  font-size: 0.9rem;
  font-weight: 500;
  border: 1px solid var(--glass-border);
  box-shadow: var(--shadow-glow-cyan);
  z-index: 1000;
  animation: pulse-glow 1.5s infinite;
}

.connection-status.connected { 
  color: var(--neon-green); 
  box-shadow: var(--shadow-glow-green);
}
.connection-status.disconnected { 
  color: var(--neon-red); 
  box-shadow: var(--shadow-glow-red);
}

.status-indicator {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: currentColor;
  animation: pulse 1s infinite;
  box-shadow: 0 0 10px currentColor;
}

@keyframes pulse-glow {
  0%, 100% { box-shadow: var(--shadow-glow-cyan); }
  50% { box-shadow: 0 0 50px var(--neon-cyan), 0 0 100px var(--neon-cyan); }
}

/* Dashboard Header with Full Body Integration */
.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: var(--spacing-6) var(--spacing-4);
  background: var(--glass-bg);
  backdrop-filter: blur(40px);
  padding: var(--spacing-3);
  border-radius: var(--radius-lg);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.dashboard-header::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, transparent, rgba(0,255,255,0.2), transparent);
  animation: header-shimmer 5s infinite;
  z-index: -1;
}

@keyframes header-shimmer {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

.header-left h2 {
  font-size: 2rem;
  font-weight: 700;
  margin: 0;
  background: linear-gradient(45deg, var(--neon-cyan), var(--neon-purple), var(--neon-blue), var(--neon-pink));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 400% 400%;
  animation: gradient-shift 2s ease infinite;
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
}

@keyframes gradient-shift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

@keyframes icon-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.last-update, .current-time {
  color: var(--text-secondary);
  font-size: 1rem;
  margin: 0.5rem 0 0 0;
  font-family: var(--font-family-cyber);
  animation: flicker 2s infinite;
}

@keyframes flicker {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.8; }
}

.loading-text, .error-text {
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
  animation: scanline 2s infinite;
}

.loading-text {
  color: var(--neon-cyan);
}

.error-text {
  color: var(--neon-red);
  text-shadow: var(--shadow-glow-red);
}

@keyframes scanline {
  0%, 100% { text-shadow: 0 0 5px var(--neon-cyan); }
  50% { text-shadow: 0 0 20px var(--neon-cyan); }
}

.header-actions {
  display: flex;
  gap: var(--spacing-2);
  align-items: center;
}

.time-range-selector {
  padding: var(--spacing-2) var(--spacing-3);
  border: 1px solid var(--neon-cyan);
  border-radius: var(--radius-md);
  background: var(--glass-bg);
  color: var(--text-primary);
  font-size: 0.9rem;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-glow-cyan);
  font-family: var(--font-family-cyber);
}

.time-range-selector:focus {
  outline: none;
  box-shadow: var(--shadow-glow-purple);
  border-color: var(--neon-purple);
  animation: button-glow 1s infinite;
}

@keyframes button-glow {
  0%, 100% { box-shadow: var(--shadow-glow-purple); }
  50% { box-shadow: 0 0 40px var(--neon-purple); }
}

.refresh-button {
  background: linear-gradient(45deg, var(--neon-cyan), var(--neon-blue));
  color: var(--text-primary);
  padding: var(--spacing-2) var(--spacing-4);
  border-radius: var(--radius-md);
  font-weight: 600;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: var(--shadow-glow-cyan);
  font-family: var(--font-family-cyber);
}

.refresh-button:hover:not(:disabled) {
  background: linear-gradient(45deg, var(--neon-purple), var(--neon-pink));
  box-shadow: var(--shadow-glow-purple);
  transform: translateY(-3px) scale(1.05);
  animation: button-pulse 0.5s infinite;
}

.refresh-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@keyframes button-pulse {
  0%, 100% { transform: translateY(-3px) scale(1.05); }
  50% { transform: translateY(-5px) scale(1.1); }
}

/* 3D Visualization Section */
.three-section {
  margin: var(--spacing-6) var(--spacing-4);
  z-index: 1;
  position: relative;
}

.three-section h3 {
  font-size: 1.5rem;
  text-align: center;
  margin-bottom: var(--spacing-4);
  color: var(--neon-cyan);
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
  animation: header-scan 2s infinite;
}

@keyframes header-scan {
  0% { text-shadow: var(--shadow-glow-cyan); }
  50% { text-shadow: 0 0 30px var(--neon-cyan); }
  100% { text-shadow: var(--shadow-glow-cyan); }
}

.three-container {
  width: 100%;
  height: 300px;
  border-radius: var(--radius-lg);
  border: 1px solid var(--glass-border);
  background: var(--glass-bg);
  backdrop-filter: blur(40px);
  box-shadow: var(--glass-shadow);
  overflow: hidden;
}

/* System Health Section */
.system-health {
  margin: var(--spacing-6) var(--spacing-4);
  z-index: 1;
}

.health-card {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--glass-bg);
  backdrop-filter: blur(40px);
  padding: var(--spacing-3);
  border-radius: var(--radius-lg);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  position: relative;
}

.health-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(16,185,129,0.2) 0%, transparent 70%);
  z-index: -1;
}

.health-icon {
  font-size: 2.5rem;
  text-shadow: var(--shadow-glow-green);
  animation: health-pulse 2s infinite;
  filter: drop-shadow(0 0 20px currentColor);
}

@keyframes health-pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.health-info h3 {
  font-size: 1.5rem;
  margin: 0 0 0.5rem 0;
  color: var(--text-primary);
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-green);
}

.health-score {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0;
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-green);
}

.health-progress-bar {
  width: 100%;
  height: 8px;
  background: var(--glass-bg);
  border-radius: var(--radius-sm);
  overflow: hidden;
  border: 1px solid var(--glass-border);
  box-shadow: inset 0 0 10px rgba(0,0,0,0.5);
}

.health-progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--neon-green), var(--neon-yellow));
  border-radius: var(--radius-sm);
  transition: width 1s ease;
  box-shadow: var(--shadow-glow-green);
  animation: progress-glow 2s infinite;
}

@keyframes progress-glow {
  0%, 100% { box-shadow: var(--shadow-glow-green); }
  50% { box-shadow: 0 0 20px var(--neon-green); }
}

/* Enhanced Metrics Grid */
.metrics-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: var(--spacing-4);
  margin: var(--spacing-6) var(--spacing-4);
  z-index: 1;
}

.metric-card {
  background: var(--glass-bg);
  backdrop-filter: blur(40px);
  padding: var(--spacing-3);
  border-radius: var(--radius-lg);
  border: 1px solid var(--glass-border);
  transition: all 0.4s ease;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  box-shadow: var(--glass-shadow);
  animation: card-enter 0.5s ease forwards;
  opacity: 0;
  transform: translateY(50px);
}

.metric-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: 
    linear-gradient(90deg, transparent, rgba(0,255,255,0.2), transparent),
    repeating-linear-gradient(
      0deg,
      transparent,
      transparent 2px,
      rgba(0, 255, 255, 0.1) 2px,
      rgba(0, 255, 255, 0.1) 4px
    );
  transition: left 0.5s ease;
  animation: wireframe-scan 3s linear infinite;
  z-index: -1;
}

@keyframes wireframe-scan {
  0% { background-position: 0% 0%, 0% 0%; }
  100% { background-position: 100% 100%, 100% 100%; }
}

.metric-card:nth-child(1) { animation-delay: 0.1s; }
.metric-card:nth-child(2) { animation-delay: 0.2s; }
.metric-card:nth-child(3) { animation-delay: 0.3s; }
.metric-card:nth-child(4) { animation-delay: 0.4s; }
.metric-card:nth-child(5) { animation-delay: 0.5s; }
.metric-card:nth-child(6) { animation-delay: 0.6s; }

@keyframes card-enter {
  to { opacity: 1; transform: translateY(0); }
}

.metric-card:hover::before {
  left: 100%;
}

.metric-card:hover {
  transform: translateY(-5px) rotateX(5deg) rotateY(3deg);
  box-shadow: var(--shadow-glow-cyan);
  border-color: var(--neon-cyan);
  animation: card-hover 0.3s ease;
}

@keyframes card-hover {
  0% { transform: translateY(0) rotateX(0) rotateY(0); }
  100% { transform: translateY(-5px) rotateX(5deg) rotateY(3deg); }
}

.metric-icon {
  font-size: 2.5rem;
  margin-bottom: var(--spacing-3);
  color: var(--neon-cyan);
  text-shadow: var(--shadow-glow-cyan);
  animation: float 4s ease-in-out infinite, icon-pulse 2s infinite;
  filter: drop-shadow(0 0 10px currentColor);
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  33% { transform: translateY(-15px) rotate(5deg); }
  66% { transform: translateY(-5px) rotate(-5deg); }
}

.metric-card h3 {
  font-size: 1rem;
  margin-bottom: var(--spacing-3);
  color: var(--text-secondary);
  text-transform: uppercase;
  font-weight: 600;
  font-family: var(--font-family-cyber);
  letter-spacing: 3px;
  animation: scanline 2s infinite;
}

@keyframes scanline {
  0%, 100% { text-shadow: 0 0 5px var(--neon-cyan); }
  50% { text-shadow: 0 0 20px var(--neon-cyan); }
}

.metric-value {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: var(--spacing-2);
  background: linear-gradient(45deg, var(--neon-cyan), var(--neon-purple), var(--neon-blue));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  background-size: 300% 300%;
  animation: value-gradient 3s ease infinite;
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
}

@keyframes value-gradient {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

.metric-3d-chart {
  height: 20px;
  background: var(--glass-bg);
  border-radius: var(--radius-sm);
  overflow: hidden;
  margin: var(--spacing-2) 0;
  border: 1px solid var(--glass-border);
}

.metric-bar {
  height: 100%;
  background: linear-gradient(90deg, var(--neon-green), var(--neon-yellow));
  width: 75%;
  border-radius: var(--radius-sm);
  animation: bar-fill 2s ease;
  box-shadow: var(--shadow-glow-green);
}

@keyframes bar-fill {
  0% { width: 0%; }
  100% { width: 75%; }
}

.trend {
  font-weight: 500;
  text-shadow: 0 0 15px currentColor;
  animation: change-pulse 1s infinite;
  font-family: var(--font-family-cyber);
}

@keyframes change-pulse {
  0%, 100% { opacity: 1; }
  50% { opacity: 0.7; }
}

.trend.positive { color: var(--neon-green); box-shadow: 0 0 15px var(--neon-green); }
.trend.negative { color: var(--neon-red); box-shadow: 0 0 15px var(--neon-red); }

/* Alerts Section Enhanced */
.alerts-section {
  background: var(--glass-bg);
  backdrop-filter: blur(20px);
  border-radius: var(--radius-lg);
  padding: var(--spacing-4);
  border: 1px solid var(--glass-border);
  box-shadow: var(--glass-shadow);
  margin: var(--spacing-6) var(--spacing-4);
  z-index: 1;
}

.alerts-section h3 {
  font-size: 1.5rem;
  margin-bottom: var(--spacing-3);
  color: var(--neon-cyan);
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
  animation: header-scan 2s infinite;
}

@keyframes header-scan {
  0% { text-shadow: var(--shadow-glow-cyan); }
  50% { text-shadow: 0 0 30px var(--neon-cyan); }
  100% { text-shadow: var(--shadow-glow-cyan); }
}

.alerts-list {
  max-height: 300px;
  overflow-y: auto;
  padding-right: var(--spacing-2);
}

.alerts-list::-webkit-scrollbar {
  width: 6px;
}

.alerts-list::-webkit-scrollbar-track {
  background: var(--glass-bg);
  border-radius: var(--radius-sm);
}

.alerts-list::-webkit-scrollbar-thumb {
  background: var(--neon-cyan);
  border-radius: var(--radius-sm);
  box-shadow: var(--shadow-glow-cyan);
}

.alert-item {
  padding: var(--spacing-3);
  border-radius: var(--radius-md);
  margin-bottom: var(--spacing-2);
  border-left: 5px solid var(--neon-blue);
  background: rgba(59, 130, 246, 0.15);
  border: 1px solid var(--glass-border);
  transition: all 0.4s ease;
  position: relative;
  overflow: hidden;
}

.alert-item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background: linear-gradient(90deg, var(--neon-blue), transparent);
  animation: alert-progress 3s linear infinite;
}

@keyframes alert-progress {
  0% { width: 0%; }
  100% { width: 100%; }
}

.alert-item:hover {
  box-shadow: var(--shadow-glow-blue);
  transform: translateX(10px) scale(1.02);
  border-left-width: 8px;
}

.alert-item.info { border-left-color: var(--neon-blue); background: rgba(59, 130, 246, 0.15); }
.alert-item.warning { border-left-color: var(--neon-yellow); background: rgba(245, 158, 11, 0.15); }
.alert-item.critical { border-left-color: var(--neon-red); background: rgba(239, 68, 68, 0.15); }

.alert-time {
  font-size: 0.85rem;
  color: var(--text-secondary);
  margin-bottom: 0.5rem;
  font-family: var(--font-family-cyber);
}

.alert-message {
  font-weight: 500;
  color: var(--text-primary);
  margin: 0;
  text-shadow: 0 0 5px currentColor;
}

/* Enhanced Drilldown Modal */
.drilldown-modal {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(20px);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: modal-bg 0.4s ease;
}

@keyframes modal-bg {
  from { background: rgba(0, 0, 0, 0); }
  to { background: rgba(0, 0, 0, 0.9); }
}

.drilldown-content {
  background: var(--glass-bg);
  backdrop-filter: blur(40px);
  padding: var(--spacing-5);
  border-radius: var(--radius-lg);
  max-width: 700px;
  width: 90%;
  max-height: 85vh;
  overflow-y: auto;
  position: relative;
  border: 2px solid var(--glass-border);
  box-shadow: var(--shadow-glow-purple);
  animation: modal-enter 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

@keyframes modal-enter {
  from { opacity: 0; transform: scale(0.8) rotateY(180deg) translateZ(-100px); }
  to { opacity: 1; transform: scale(1) rotateY(0deg) translateZ(0); }
}

.drilldown-content h3 {
  color: var(--neon-cyan);
  font-family: var(--font-family-cyber);
  text-shadow: var(--shadow-glow-cyan);
  margin-bottom: var(--spacing-3);
}

.drilldown-content pre {
  background: var(--glass-bg);
  border: 1px solid var(--glass-border);
  border-radius: var(--radius-md);
  padding: var(--spacing-3);
  color: var(--text-primary);
  font-family: var(--font-family-cyber);
}
